
// ALL IMPORTS - All important stuff I have made and isnt automatically generated will be found in "components"
import Header from './Components/Header'
import Title from './Components/Title'
import Footer from './Components/Footer'
import { useState } from 'react'


function App() {

  //Header
  let [colnumHeader, numSetterHeader] = useState(0)
  let [fontnumHeader, numSetterFontHeader] = useState(0)
  
  //Body
  let [colnumBody, numSetterBody] = useState(0)
  let [fontnumBody, numSetterFontBody] = useState(0)


  //Footer
  let [colnumFooter, numSetterFooter] = useState(0)
  let [fontnumFooter, numSetterFontFooter] = useState(0)



  let cols = ["blue-200","blue-300","blue-400","blue-500","blue-600","blue-700","blue-800","blue-900",
        "red-200","red-300","red-400","red-500","red-600","red-700","red-800","red-900",
      "gray-200","gray-300","gray-400","gray-500","gray-600","gray-700","gray-800","gray-900",
        "yellow-200","yellow-300","yellow-400","yellow-500","yellow-600","yellow-700","yellow-800","yellow-900",
        "green-200","green-300","green-400","green-500","green-600","green-700","green-800","green-900",
        "indigo-200","indigo-300","indigo-400","indigo-500","indigo-600","indigo-700","indigo-800","indigo-900",
      "purple-200","purple-300","purple-400","purple-500","purple-600","purple-700","purple-800","purple-900",
      "pink-200","pink-300","pink-400","pink-500","pink-600","pink-700","pink-800","pink-900"

] //Use colour for footer too
  let textFonts = ["sans","serif","roboto","arial","optima"]

  if(colnumHeader >= cols.length){colnumHeader = 0}

  if(colnumBody >= cols.length){colnumBody = 0}

  if(colnumFooter >= cols.length){colnumFooter = 0}


  if(fontnumHeader >= textFonts.length){fontnumHeader = 0}
  
  if(fontnumBody >= textFonts.length){fontnumBody = 0}

  if(fontnumFooter >= textFonts.length){fontnumFooter = 0}

  if(colnumHeader <= -1){colnumHeader = cols.length -1}

  if(colnumBody <= -1){colnumBody = cols.length -1}

  if(colnumFooter <= -1){colnumFooter = cols.length -1}


  if(fontnumHeader  <= -1){fontnumHeader = textFonts.length -1}
  
  if(fontnumBody  <= -1){fontnumBody = textFonts.length -1}

  if(fontnumFooter  <= -1){fontnumFooter = textFonts.length -1}
  
  return (

    <div>

    <Header name={cols[colnumHeader]}/>


    {/* Plus Buttons */}
    <button  onClick={() => numSetterHeader(colnumHeader + 1)} className="sticky  float-right p-2 text-white font-bold  " >C+</button>
    <button  onClick={() => numSetterFontHeader(fontnumHeader + 1)} className="sticky  float-left py-2 ml-20 text-white font-bold  " >F+</button>


    <button  onClick={() => numSetterBody(colnumBody + 1)} className="sticky  float-right my-20 text-white font-bold   " >C+</button>
    <button  onClick={() => numSetterFontBody(fontnumBody + 1)} className="sticky  float-left my-20  text-white font-bold   " >F+</button>

    <button  onClick={() => numSetterFooter(colnumFooter + 1)} className="sticky  float-right text-white font-bold  " style={{marginTop:500}}>C+</button>
    <button  onClick={() => numSetterFontFooter(fontnumFooter + 1)} className="sticky  float-left  text-white font-bold  " style={{marginTop:500}}>F+</button>


    {/*Minus Buttons */}


    <button  onClick={() => numSetterHeader(colnumHeader - 1)} className="sticky -ml-4 float-right p-2 text-white font-bold  " >C-</button>
    <button  onClick={() => numSetterFontHeader(fontnumHeader - 1)} className="sticky  float-left py-2 ml-20 text-white font-bold  " >F-</button>


    <button  onClick={() => numSetterBody(colnumBody - 1)} className="sticky -ml-4 float-right my-20 text-white font-bold   " >C-</button>
    <button  onClick={() => numSetterFontBody(fontnumBody - 1)} className="sticky  float-left my-20  text-white font-bold   " >F-</button>

    <button  onClick={() => numSetterFooter(colnumFooter - 1)} className="sticky -ml-4 float-right text-white font-bold  " style={{marginTop:500}}>C-</button>
    <button  onClick={() => numSetterFontFooter(fontnumFooter - 1)} className="sticky  float-left  text-white font-bold  " style={{marginTop:500}}>F-</button>

    <Title name={cols[colnumBody]}/>

   
  

  

    <Footer name={cols[colnumFooter]} font={textFonts[fontnumFooter]} />
    </div>
  );
}

export default App;
